import React, { Component } from 'react'
import styles from './search.module.scss'

class Search extends Component {
  constructor(props) {
    super(props)
    this.state = { 
      changeIndex: 0,
      tags: [{
        name: '前端'
      },{
        name: 'node'
      },{
        name: 'js'
      },{
        name: 'react'
      },{
        name: 'vue'
      },{
        name: '其他'
      }]
     }
  }
  changeIndex(index) {
    this.setState({
      changeIndex: index
    })
  }
  render() { 
    return ( 
      <div className={styles['search-wrap']}>
        <ul>
          {
            this.state.tags.map((item, index) => {
              return <li className={this.state.changeIndex === index ?styles.active : ''} key={index} onClick={() => this.changeIndex(index)}>{item.name}</li>
            })
          }
        </ul>
        <span className={styles['search-bar']}>
          <input className={styles['search-content']}></input>
          <input value='搜索' className={styles['search-btn']}></input>
        </span>
      </div>  
    )
  }
}
 
export default Search